import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

private baseUrl:String='http://localhost:8080/api/';

  constructor(private http:HttpClient) { }




loginCheck(obj){
return (this.http.post(`${this.baseUrl}logincheck`,obj));
}

getEmployee(){
 return (this.http.get(`${this.baseUrl}getAllEmployee`));
}
getEmployeeByEid(eid){
  return (this.http.get(`${this.baseUrl}getemployeebyeid/${eid}`));
}
getAllCountry(){
 return  (this.http.get(`${this.baseUrl}getAllCountry`));
}
addEmployee(obj){

  return (this.http.post(`${this.baseUrl}saveEmployee`,obj,{
    responseType:'text'
  }))
}
UpdateEmp(obj){
return (this.http.put(`${this.baseUrl}updateEmployee`,obj,{ responseType:'text'}));
}
DeleteEmp(eid){
  return (this.http.delete(`${this.baseUrl}deleteEmployee/${eid}`,{ responseType:'text'} ));
}
changeStatus(eid){
  return (this.http.get(`${this.baseUrl}changeStatus/${eid}`,{ responseType:'text'} ));

}
}
