import { Employee } from './../model/employee';
import { Component, OnInit, TemplateRef } from '@angular/core';
import { HttpService } from '../http.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { DialogService } from '../dialog.service';
import { ToastrService  } from 'ngx-toastr';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  EmpData:any[]=[];
  empobj=<Employee>{};
  isradioCheck:boolean=false;
  modalRef: BsModalRef;
  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  };

  p:number=1;
  nameSearch:string='';

  constructor(private service:HttpService,
    private modalservice:BsModalService,
    private dialogservice:DialogService,
    private toaster:ToastrService) { }
 

  ngOnInit() {
   this.GetEmpData();
  }

GetEmpData(){
  this.service.getEmployee()
  .subscribe((response)=>{
    this.EmpData=(<any>response);
  })
}

onRadioClick(item){
  // console.log('Click event ');
  this.empobj=item;
  console.log(this.empobj);
  this.isradioCheck=true;
}
onRadioCheck():boolean{
  if(this.isradioCheck)
  return true;
  else
  return false
}

onUpdate(popup: TemplateRef<any>){
  if(this.onRadioCheck())
  {

    this.modalRef = this.modalservice.show(        
      popup, this.config);
    //perform a logic
  }else{
    alert('Please Select A Record To Update..')
  }
}
onDelete(){
  if(this.onRadioCheck()){

    //write ur code here
    this.dialogservice.OpenConfirmDialog('Are you sure to delete this record?')
    .afterClosed()
    .subscribe((res)=>{
      if(res){
        //delete the record
        // console.log('====>>> '+res)
        this.service.DeleteEmp(this.empobj.eid)
        .subscribe((response)=>{
          console.log(response);
          this.toaster.success(response)
         this.GetEmpData();
        })


      }else{
        //no need any action
        // console.log('++++++++ >>> '+res)
      }
    })

  }else{
    // alert('Please Select A Record To Delete..')
    
    this.toaster.error('Please Select A Record To Delete..')
  }
}
OnChangeStatus(){
  if(this.onRadioCheck()){
    this.dialogservice.OpenConfirmDialog('Are you sure to change status ?')
    .afterClosed()
    .subscribe((res)=>{
      if(res){
        if(this.empobj.status.toLocaleLowerCase()=='suspend'){
this.toaster.warning('this Employee is Suspend You Can Not Change Status '+this.empobj.eid)
        }else{
          this.service.changeStatus(this.empobj.eid)
          .subscribe((response)=>{
            console.log(response);
            this.toaster.success(response)
            this.GetEmpData();
          })
        }
        
      }else{
        //no need any action
        // console.log('++++++++ >>> '+res)
      }
    })
  }else{
    // alert('Please Select A Record To Delete..')
    
    this.toaster.error('Please Select A Record To Change Statuse..')
  }
}

}
